import React from "react";
import { ProjectListScreen } from "screens/project-list";
import { useAuth } from "context/auth-context";
import styled from "@emotion/styled";
import { ButtonNoPadding, Row } from "components/lib";
import { ReactComponent as SoftwareLogo } from "assets/software-logo.svg";
import { Button, Dropdown, Menu } from "antd";
import { Navigate, Route, Routes } from "react-router";
import { ProjectScreen } from "screens/project";
import { BrowserRouter as Router } from "react-router-dom";
import { restRoute } from "utils";
import { ProjectModal } from "screens/project-list/project-modal";
import { ProjectPopvoer } from "components/project-popvoer";
import { UserPopover } from "components/user-popover";

/*
 * grid 和 flex 的各自使用场景
 *  1. 是一维布局 还是二维布局
 *    一般来说，一维布局用 flex，二维布局用 grid
 *  2. 是从内容出发还是从布局出发
 *    从内容出发：你先有一组内容（数量一般不确定），然后希望他们均与的分布在容器中，由内容自己的大小决定空间
 *    从布局出发：先规划网格（数量一般比较固定），然后把元素往里填充
 *    从内容出发，用 flex
 *    从布局出发，用 grid
 *
 *  */

export const AuthenticatedApp = () => {
  return (
    <Container>
      <Router>
        <PageHeader />
        <Main>
          <Routes>
            <Route path={"/projects"} element={<ProjectListScreen />} />
            <Route
              path={"/projects/:projectId/*"}
              element={<ProjectScreen />}
            />
            <Navigate to={"/projects"} />
          </Routes>
        </Main>
        <ProjectModal />
      </Router>
    </Container>
  );
};

const PageHeader = () => {
  return (
    <Header between={true}>
      <HeaderLeft gap={true}>
        <ButtonNoPadding type={"link"} onClick={restRoute}>
          <SoftwareLogo width={"18rem"} color={"rgb(38,132,255"} />
        </ButtonNoPadding>
        <ProjectPopvoer />
        <UserPopover />
      </HeaderLeft>
      <HeaderRight>
        <User />
      </HeaderRight>
    </Header>
  );
};

const User = () => {
  const { logout, user } = useAuth();
  return (
    <Dropdown
      overlay={
        <Menu>
          <Menu.Item key={"logout"}>
            <Button type={"link"} onClick={logout}>
              登出
            </Button>
          </Menu.Item>
        </Menu>
      }
    >
      <Button type={"link"} onClick={(e) => e.preventDefault()}>
        Hi, {user?.name}
      </Button>
    </Dropdown>
  );
};

const Container = styled.div`
  display: grid;
  grid-template-rows: 6rem 1fr;
  height: 100vh;
`;

const Header = styled(Row)`
  padding: 3.2rem;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1);
  z-index: 1;
`;

const HeaderLeft = styled(Row)``;

const HeaderRight = styled.div``;

const Main = styled.main`
  height: calc(100vh - 6rem);
  display: flex;
`;
